	module.exports = {"data":{"site":{"siteMetadata":{"title":"yerevancoder","author":"Coders of Armenia"}},"markdownRemark":{"id":"/Users/edgarkhanzadian/Documents/yerevancoder.github.io/src/pages/2017-12-23-tech-places-and-events/index.md absPath of file >>> MarkdownRemark","html":"<p><em>By Edgar Aroutiounian</em>,\ncatch me on <a href='https://twitter.com/@edgararout'>twitter</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">(</span><span class=\"token keyword\">async</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">await</span> <span class=\"token function\">land_in_armenia</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">await</span> <span class=\"token function\">look_for_events</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">await</span> <span class=\"token function\">make_new_friends</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<p>So you just landed in Yerevan and need a quick way to get a pulse of\nthe tech industry ASAP. Here’s your insider’s crash course</p>\n<h1>Places for tech</h1>\n<h2>Yerevan-based</h2>\n<ol>\n<li><a href=\"http://istc.am\">ISTC</a>-The Innovative Solutions and Technologies\nCenter hosts many many tech events featuring some of the best minds\nin the local Data Science scene and is home to quite a few startups.</li>\n<li><a href=\"http://www.micarmenia.am\">MIC-Armenia</a>-Microsoft Innovation Center\nArmenia holds many trainings, hosts talks.</li>\n<li><a href=\"https://aua.am\">AUA</a>-Has a good computer science department,\ncomparable to a top 200 USA university. They also host many tech\nevents. </li>\n<li><a href=\"https://www.smartgate.vc/single-post/2017/12/22/Better-together-Hero-House-is-introduced-to-the-startup-community-and-friends\">HeroHouse</a>-An\nexciting and brand new initiative for startups in Armenia</li>\n<li><a href=\"https://tumo.org\">Tumo</a>-A truly remarkable and unique place in the\nworld, Tumo hosts many tech events and startups.</li>\n</ol>\n<h2>Outside Yerevan</h2>\n<p>Outside the capital, there are less tech opportunities but they are\ngrowing daily. Gyumri has its own Tumo, Loft and the\n<a href=\"http://gtc.am/en/home/\">GTC</a>. Similarly, Vanadzor also has new\ntechnology center: <a href=\"http://vtc.am/en/\">VTC</a>. In Artsakh, there is a\nTumo center as well.</p>\n<h1>Places coders hangout at</h1>\n<h2>Yerevan-based</h2>\n<ol>\n<li><a href=\"http://www.achajour.am/en/\">Achajour</a>-This is a cool cafe, usually many\nEnglish speakers and many coders, prime place to make friends and\nrun into familiar faces.</li>\n<li><a href=\"https://www.facebook.com/The.LOFT.center/\">Loft</a>-The best place to\nget serious work done but usually just overnight. Overnight deal is\nfantastic, only 1000 dram (~$2) for the entire night from 12AM-7AM,\nunlimited coffee and snacks.</li>\n</ol>\n<h1>Discussion groups</h1>\n<ol>\n<li><a href=\"https://www.facebook.com/groups/410797219090898/\">iterate hackerspace</a>-Facebook group.</li>\n</ol>\n<h1>Tech events 2018</h1>\n<h2>Hackathons</h2>\n<h3>January</h3>\n<ol>\n<li><a href=\"https://www.facebook.com/events/407926479641201/permalink/410025256097990/\">Global Game Jam Armenia 2018</a>-January 26, 2018</li>\n</ol>\n<h2>Meetups</h2>\n<h3>January</h3>\n<ol>\n<li><a href=\"https://www.facebook.com/events/1811743828899726/\">GEEK Girls’ Dinner: Welcome to 2018</a>-January 7, 2018</li>\n</ol>","frontmatter":{"title":"Tech places and events in Armenia","date":"December 23, 2017","discussionId":"2017-12-23-tech-places-events"}}},"pathContext":{"slug":"/2017-12-23-tech-places-and-events/"}}

